.catalog {
  display: flex;
  flex-wrap: wrap;
  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    $card-margin: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: $card-margin;
    transition-duration: 300ms;
    transition-property: color transform;

    &:hover {
      color: #34568b;
      transform: scale(1.2);
    }

    &:not(:nth-child(4n)) {
      margin-right: $card-margin;
    }
  }
}
